import { useState, useEffect } from "react";

import classes from "./businessSetupResume.module.scss";

import Header from "../../Components/Header/header";
//import RightArrow from "../../assets/icons/rightArrow-1.svg";

import RightArrow from "../../assets/icons/rightArrow-1.svg";

import { Link } from "react-router-dom";

import { useToastMessage } from "../../contexts";

import { getPreBusinessById } from "../../api/preBusiness";

import { getUserDetail } from "../../api/user";

const BusinessSetupResume = () => {
  const toastMessage = useToastMessage();
  const [userName, setUserName] = useState("");

  const preload = async () => {
    const businessId = localStorage.getItem("businessId");
    if (businessId) {
      const res = await getPreBusinessById(businessId);
      if (res.error) {
        return toastMessage.setErrorMessage(res.error);
      }
    }
    const user = await getUserDetail();
    setUserName(user.Name);
  };

  useEffect(() => {
    preload();
  }, []);

  return (
    <div className="billingOuterWrapper">
      <div className={classes.contactParentWrapper}>
        <div className={classes.firstHalf}>
          <Header />
        </div>
        <div className={classes.secondHalf}>
          <div className={classes.sideDrawerWrapper}>
            <div className={classes.sideDrawerHeaderWrapper}>
              <div className={classes.sideDrawerHeadingWrapper}>
                <div className={classes.sideDrawerTitle}>
                  Welcome back {userName}
                </div>
              </div>
            </div>
            <div className={classes.formContainter}>
              <p>
                You have to setup your business and social profiles to use the
                easysocial platform.
              </p>
              <Link to="business-setup" className={classes.setupLink}>
                <div className={classes.buttonWrapIcon}>
                  <div>Resume business setup</div>

                  <div>
                    <img src={RightArrow} alt="right arrow" />
                  </div>
                </div>
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default BusinessSetupResume;
