import React from "react";

//import classes from './header.module.scss';

import classes from "./header.module.css";

//import Logo from '../../Assets/icons/easySocial.svg'
import Logo from "../../assets/Logo/easySocial.svg";
export default function Header({ payment = false }) {
  return (
    <div
      className={
        payment ? classes.headerOuterWrapperSpecial : classes.headerOuterWrapper
      }
    >
      <div className={classes.headerWrapper}>
        <img src={Logo} alt="logo" />
        {/* <div className={classes.logoTitle}>Social Marketing Simplified</div> */}
      </div>
    </div>
  );
}
