/** @format */

import { AuthenticatedHeaders } from "utils/ApiHeaders";
import { API, API_V2 } from "../Backend";

import encryptResponse from "../Shared/encryptResponse";

export const createPreBusiness = async (data) => {
  try {
    const res = await fetch(`${API}/pre-business/create`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "Content-Type": "application/json",
      },
      body: JSON.stringify(encryptResponse(data)),
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

